import React, { useState } from 'react';
import { withRouter } from 'react-router-dom';
import {
  Steps,
  // Hints
} from 'intro.js-react';
import 'intro.js/introjs.css';
const IntroIndex = (props) => {
  const introV = '0.0.1'
  const [stepsEnabled, setStepsEnabled] = useState(localStorage.getItem('introV') !== introV);
  const initialStep = 0;
  const steps = [
    {
      title: '',
      element: '.menu-player',
      intro: <>
        <p></p>
      </>,
      href: '/'
    }, {
      title: '',
      element: '.menu-support',
      intro: <>
        <p></p>
        <p></p>
        <p></p>
        <p></p>
      </>,
      href: '/support'
    }, {
      title: '',
      element: '.menu-skill',
      intro: <>
        <p></p>
        <p></p>
        <p></p>
        <p></p>
      </>,
      href: '/skill'
    }, {
      title: '',
      element: '.menu-race',
      intro: <>
        <p></p>
      </>,
      href: '/race'
    }, {
      title: '',
      element: '.menu-nurturing2',
      intro: <>
        <p></p>
        <p></p>
        <p></p>
      </>,
      href: '/nurturing2'
    }, {
      title: '',
      element: '.menu-seed',
      intro: <>
        <p></p>
        <p></p>
      </>,
      href: '/seed'
    }, {
      title: '',
      element: '.reset-nur',
      intro: <>
        <p></p>
        <p></p>
      </>,
    }, {
      title: '',
      element: '.reset-intro',
      intro: <>
        <p></p>
      </>,
    }]

  const onExit = () => {
    localStorage.setItem('introV', introV)
    setStepsEnabled(false)
  }
  const onBeforeChange = (nextStepIndex) => {
    steps[nextStepIndex].href && props.history.push(steps[nextStepIndex].href)
  }
  const onComplete = () => {
    localStorage.setItem('introV', introV)
    setStepsEnabled(false)
  }
  return (
    <Steps
      enabled={stepsEnabled}
      steps={steps}
      initialStep={initialStep}
      onExit={onExit}
      onBeforeChange={onBeforeChange}
      onComplete={onComplete}
      options={{
        doneLabel: 'Done'
      }}
    />
  )
}
export default withRouter(IntroIndex)
